<script setup async>
import { required, helpers } from '@vuelidate/validators'
import { useIntegrationTremendous } from '@/store/pinia/integration/tremendeous'

import Select from '@/components/Select.vue'
import IntegrationTremendousAccordionHowItWork
	from '@/components/Features/Integration/IntegrationTremendous/IntegrationTremendousAccordionHowItWork/IntegrationTremendousAccordionHowItWork.vue'
import { useVuelidate } from '@vuelidate/core'

const integrationTremendousStore = useIntegrationTremendous()
const validation = useVuelidate({
	selectedCampaignId: { required: helpers.withMessage('Campaign template is required', required) },
}, integrationTremendousStore.$state.form, {
	$lazy: true
})

const onValidate = () => {
	validation.value.$validate()

	return !validation.value.$invalid
}

defineExpose({
	onValidate
})
</script>

<template>
	<div class="space-y-3">
		<div class="mb-4">
			<label for="campaign-templates">Campaign Templates</label>
			<Select v-model="integrationTremendousStore.$state.form.selectedCampaignId"
				:items="integrationTremendousStore.$state.get.result.campaigns"
				name="name"
				by="id"
				block
				searchable
			/>
			<small class="text-red-base block mt-1" v-for="(error, errorIndex) of validation.selectedCampaignId.$errors" :key="errorIndex">{{ error.$message }}.</small>
		</div>
		<p>Please refer to detailed instruction below.</p>
		<IntegrationTremendousAccordionHowItWork />
	</div>
</template>
